.navbar__list {
  display: flex;
  background-color: #F5717F;
  justify-content: space-around;
  padding: 1rem;
}
.navbar__list__item {
  font-weight: bold;
  list-style: none;
}
.navbar__list__item__link {
  color: white;
  text-decoration: none;
}
.navbar__list__item:hover {
  text-decoration: underline;
}

@media screen and (max-width: 900px) {
  .navbar {
    display: none;
  }
}

